import React from "react";
import { useRef } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const Login = () => {
  console.log("entered"); //check
  const idRef = useRef();
  const pwRef = useRef();

  const navigate = useNavigate();

  const handleLogin = () => {
    console.log("login handler called");
    if (idRef.current.value === "" || idRef.current.value === undefined) {
      alert(" !!!");
      idRef.current.focus();
      return false;
    }
    if (pwRef.current.value === "" || pwRef.current.value === undefined) {
      alert(" !!!");
      pwRef.current.focus();
      return false;
    }

    console.log("now id:", idRef.current.value, "now pw:", pwRef.current.value);
    console.log(
      "LoginForm:window.sessionStorage(login_id) =>",
      window.sessionStorage.getItem("id")
    );

    const authObj = {
      id: idRef.current.value,
      password: pwRef.current.value,
    };

    axios
      .post("/auth/login_process", authObj)
      .then((res) => {
        console.log("handleLogin =>", res.data);
        if (res.data === "Customer") {
          //logined
          console.log("login as customer");
          window.sessionStorage.setItem("id", idRef.current.value); //  key : id , value : idRef.current.value 
          // sessionsStorage   , localStorage 
          navigate("/menu");
        } else if (res.data === "Manager") {
          console.log("login as manager");
          window.sessionStorage.setItem("id", idRef.current.value);
          navigate("/");
          //home   navbar  ordermanage,ingredient manage  ?
        } else {
          alert(",   .");
          idRef.current.value = "";
          pwRef.current.value = "";
          //navigate("/home");
        }
      })
      .catch((e) => {
        console.error(e);
      });
  };

  const handleMemberForm = () => {
    navigate("/Register"); //  url  
  };

  return (
    <div>
      <p></p>
      <form>
        <table border="1" width="300px" align="center">
          <tr>
            <td width="100px"></td>
            <td align="left" width="200px">
              <input
                type="text"
                name="id"
                size="20"
                ref={idRef}
                placeholder=" "></input>
            </td>
          </tr>
          <tr>
            <td width="100px"></td>
            <td align="left" width="200px">
              <input
                type="password"
                name="pw"
                size="20"
                ref={pwRef}
                placeholder=" "></input>
            </td>
          </tr>
          <tr>
            <td colSpan="2" align="center">
              <input type="button" value="" onClick={handleLogin}></input>
              &nbsp;
              <input
                type="button"
                value=""
                onClick={handleMemberForm}></input>
            </td>
          </tr>
        </table>
      </form>
    </div>
  );
};

export default Login;
